.form__container button[type=submit] {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  padding: 10px;
  width: 100%;
  max-width: 238px;
  min-height: 64px;
  font-size: 16px;
  font-weight: 500;
  line-height: 16px;
  text-transform: capitalize;
  transition: background-color, border, color 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0);
  color: #ffffff;
  background-color: #0266c1;
  user-select: none;
}
@media (max-width: 1199px) {
  .form__container button[type=submit] {
    min-height: 58px;
    margin-top: 8px;
    max-width: 100%;
  }
}
@media (max-width: 767px) {
  .form__container button[type=submit] {
    margin-top: 0;
    min-height: 52px;
  }
}
@media (hover: hover) {
  .form__container button[type=submit]:hover:not(.focus-visible) {
    outline: none;
    color: #1c3374;
    border: 1px solid #1c3374;
    background-color: #ffffff;
  }
  .form__container button[type=submit]:hover:not(.focus-visible):active {
    color: #ffffff;
    border: 1px solid #1c3374;
    background-color: #1c3374;
  }
}
.form__container button[type=submit].focus-visible:focus {
  outline: none;
  color: #1c3374;
  border: 1px solid #1c3374;
  background-color: #ffffff;
}
.form__container button[type=submit].focus-visible:focus:active {
  color: #ffffff;
  border: 1px solid #1c3374;
  background-color: #1c3374;
}
.form__container button[type=submit]:disabled {
  color: #ffffff;
  pointer-events: none;
  background: rgba(2, 102, 193, 0.1);
}

