  }
}
.form__field input::placeholder {
  color: rgba(45, 56, 63, 0.5);
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
}
.form__field input:disabled {
  border: 1px solid rgba(2, 102, 193, 0.1);
}
.form__field input--error {
  border: 1px solid #ff121f;
}

.form__field .is-valid,
.form__field .is-invalid {
  display: none;
}

.form__field .is-invalid {
  color: #ff121f;
}

.form__field.is-invalid input {
  border: 1px solid #ff121f;
}

