.tours {
  margin-bottom: 125px;
  position: relative;

  @include vp-1199 {
    margin-bottom: 86px;
  }

  @include vp-767 {
    margin-bottom: 67px;
  }

  &::before {
    @include vp-1200 {
      position: absolute;
      content: "";
      z-index: -1;
      width: 418px;
      height: 279px;
      bottom: -5.5%;
      left: 0;
      background-repeat: no-repeat;
      background-position: 0 0;
      background-size: 418px auto;
      background-image: url("../../img/decor/decor-3-desktop.png");

      @include retina {
        background-image: url("../../img/decor/decor-3-desktop@2x.png");
      }
    }
  }

  .webp & {
    &::after {
      background-image: url("../../img/decor/decor-1-desktop.webp");

      @include vp-1199 {
        background-image: url("../../img/decor/decor-1-tablet.webp");
      }

      @include vp-767 {
        background-image: url("../../img/decor/decor-1-mobile.webp");
      }

      @include retina {
        background-image: url("../../img/decor/decor-1-desktop@2x.webp");

        @include vp-1199 {
          background-image: url("../../img/decor/decor-1-tablet@2x.webp");
        }

        @include vp-767 {
          background-image: url("../../img/decor/decor-1-mobile@2x.webp");
        }
      }
    }

    @include vp-1200 {
      &::before {
        background-image: url("../../img/decor/decor-3-desktop.webp");

        @include retina {
          background-image: url("../../img/decor/decor-3-desktop@2x.webp");
        }
      }
    }
  }

  &::after {
    position: absolute;
    content: "";
    z-index: -1;
    width: 602px;
    height: 1364px;
    bottom: -114.5%;
    right: 0;
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 602px auto;
    background-image: url("../../img/decor/decor-1-desktop.png");

    @include retina {
      background-image: url("../../img/decor/decor-1-desktop@2x.png");
    }

    @include vp-1199 {
      width: 294px;
      background-size: 294px auto;
      bottom: -114.5%;
      background-image: url("../../img/decor/decor-1-tablet.png");

      @include retina {
        background-image: url("../../img/decor/decor-1-tablet@2x.png");
      }
    }

    @include vp-767 {
      width: 176px;
      height: 595px;
      bottom: -26.5%;
      background-size: 176px auto;
      background-image: url("../../img/decor/decor-1-mobile.png");

      @include retina {
        background-image: url("../../img/decor/decor-1-mobile@2x.png");
      }
    }
  }
}

.tours__container {
  display: flex;
  flex-direction: column;
}

.tours__row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 54px;

  @include vp-1199 {
    margin-bottom: 38px;
  }

  @include vp-767 {
    margin-bottom: 25px;
  }
}

.tours__title {
  margin-right: auto;

  @include vp-1199 {
    margin-top: 5px;
  }

  @include vp-767 {
    margin-top: 0;
  }
}

.tours__slider-container {
  position: relative;
  margin-bottom: 53px;

  @include vp-1199 {
    margin-bottom: 51px;
    padding-left: 6px;
    padding-right: 6px;
  }

  @include vp-767 {
    padding: 0;
    margin-bottom: 29px;
  }
}

.tours__link {
  margin: 0 auto;
  color: $tf-default;
  text-decoration: none;
  font-size: 16px;
  font-weight: 700;
  line-height: 16px;
  letter-spacing: 0.64px;
  text-transform: uppercase;
  position: relative;
  padding-right: 20px;
  user-select: none;

  @include vp-767 {
    font-size: 14px;
    line-height: 14px;
    letter-spacing: 0.56px;
    padding-right: 18px;
  }

  @include hover-focus {
    outline: none;
    color: $text-active;

    @include active {
      color: $color-main;
    }
  }

  &--disabled {
    color: rgba(2, 102, 193, 0.1);
    pointer-events: none;
  }

  & svg {
    position: absolute;
    content: "";
    width: 9px;
    height: 14px;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
  }
}
